.cont {
  display: flex;
  flex-direction: row;
  margin-left: 150px;
}

.cont1 {
  width: 46vw;
}

.cont2 {
  height: 100vh;
  width: 46vw;
  background-image: url("../images/paris.jpeg");
  background-size: cover;
  background-position: center;
}

.div2 {
  display: none;
  width: 46vw;
  flex-direction: column;
}

.h2 {
  font-weight: 800;
  font-size: 70px;
  color: #b1e5f2;
  margin-left: 30px;
  margin-top: 15%;
}

.p {
  margin-left: 30px;
  margin-bottom: 55px;
  margin-right: 40px;
}
.p1 {
  margin-left: 30px;
  margin-bottom: 55px;
  margin-right: 40px;
}

.form {
  margin-left: 30px;
  display: flex;
  flex-direction: column;
  margin-right: 30px;
}

.name {
  padding: 10px;
  margin-bottom: 5px;
  background-color: rgb(109, 109, 109);
  border: none;
  color: azure;
  font-family: "Fira Sans", sans-serif;
  font-size: 16px;
}
.email {
  padding: 10px;
  margin-bottom: 5px;
  background-color: rgb(109, 109, 109);
  border: none;
  color: azure;
  font-family: "Fira Sans", sans-serif;
  font-size: 16px;
}
.subject {
  padding: 10px;
  margin-bottom: 5px;
  background-color: rgb(109, 109, 109);
  border: none;
  color: azure;
  font-family: "Fira Sans", sans-serif;
  font-size: 16px;
}
.message {
  padding: 10px;
  background-color: rgb(109, 109, 109);
  border: none;
  color: azure;
  font-family: "Fira Sans", sans-serif;
  font-size: 16px;
}
::placeholder {
  font-size: 18px;
  color: rgb(180, 180, 180);
}

.submit {
  width: 200px;
  font-size: 20px;
  background-color: transparent;
  color: #b1e5f2;
  border: 1px solid #b1e5f2;
  padding: 15px 60px 15px 60px;
  cursor: pointer;
  margin-top: 15px;
  transition: 1s ease-in-out;
  margin-bottom: 15px;
}

.submit:hover {
  background-color: #b1e5f2;
  transition: 1s ease-in-out;
  color: black;
}

.cont2 {
  background-image: url("../images/Auckland.png");
  background-size: cover;
}

@media screen and (max-width: 1600px) {
  .h2 {
    font-size: 56px;
  }
}

@media screen and (max-width: 1296px) {
  .cont {
    margin-left: 0px;
  }
  .cont1 {
    width: 50vw;
  }
  .cont2 {
    width: 50vw;
  }
}
@media screen and (max-width: 900px) {
  .cont {
    flex-direction: column;
    background-image: none;
  }
  .cont1 {
    width: 100%;
  }

  .cont2 {
    width: 100%;
  }
}
